import { useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
// @mui
import { Card, Container, Stack, Box,Button } from '@mui/material';
// redux
import { useDispatch } from '../../redux/store';
import { getConversations, getContacts } from '../../redux/slices/chat';
// routes
import { PATH_APPLICATION } from '../../routes/paths';
// hooks
import useAuth from '../../hooks/useAuth';
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import {  UserStreamsLast, } from '../../sections/@dashboard/user/stream';

// ----------------------------------------------------------------------
export default function UserStreams() {
  const { themeStretch } = useSettings();
  const dispatch = useDispatch();
  const { user } = useAuth();
  const navigate = useNavigate();

  useEffect(() => {
    dispatch(getConversations());
    dispatch(getContacts());
  }, [dispatch]);

  return (
    <Page title="Video Show">
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <Stack justifyContent={'space-between'} direction={'row'} alignItems={'center'}>
          <HeaderBreadcrumbs
            heading="Create a new post"
            links={[
              { name: 'App', href: PATH_APPLICATION.root },
              { name: 'Profile', href: PATH_APPLICATION.profile.root },
              { name: `${user?.displayName}'s Streams` },
            ]}
            sx={{mb:3}}
          />
          <Button variant="contained" size="small"
           onClick={() => navigate(PATH_APPLICATION.profile.stream)} >Create New Stream</Button>
        </Stack>
       
        <UserStreamsLast />
      </Container>
    </Page>
  );
}







